@import url(https://fonts.googleapis.com/css?family=Muli);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  box-sizing: border-box;
  background-color: #f4f4f4;
  background-color: var(--main-bg-color);
}

:root {
  /* Color */
  --main-bg-color: #f4f4f4;
  --main-dark-bg: #002333;
  --main-purple-color: #430743;
  --main-white-color: #fff;
  --main-grey-color: #a9a9a9;
  --main-blue-color: #14b4fe;
  --fill-purple-color: #430743;
  /* Others */
  --card-br: 20px;
  --default-margin-spacing: 30px;
}

/* Content Body(layout.js) */
.content{
  margin-top: 30px;
  margin-top: var(--default-margin-spacing);
}

.analyticsSection{
  margin-bottom: 30px;
  margin-bottom: var(--default-margin-spacing);
}

/* Tables */
/* Purchase Invoice(DashBoard) */
.purchaseInvoice {
  margin-top: 30px;
  margin-top: var(--default-margin-spacing);
}

/* Analytics Page */
.contentContainer{
  font-family: Poppins, sans-serif;
}

body > .loginContainer {
  background: #fff;
}

/* Login Form */
.loginContainer {
  width: 300px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  background: #002333;
  background: var(--main-dark-bg);
  text-align: center;
}
.loginForm {
  width: 450px;
  padding: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #002333;
  background: var(--main-dark-bg);
  text-align: center;
  border-radius: 20px;
}

.loginForm h1 {
  color: #fff;
  text-transform: uppercase;
  font-weight: 500;
  padding-bottom: 20px;
}
.loginForm input[type="email"],
.loginForm input[type="password"] {
  border: 0;
  background: inherit;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 2px solid var(--fill-green-color);
  padding: 10px 10px;
  width: 250px;
  outline: none;
  color: white;
  border-radius: 24px;
  transition: 0.25s;
}
.loginForm input[type="email"]:focus,
.loginForm input[type="password"]:focus {
  width: 330px;
  border-color: #14b4fe;
  border-color: var(--main-blue-color);
}

.loginForm input[type="submit"] {
  border: 0;
  background: inherit;
  display: block;
  margin: 20px auto;
  text-align: center;
  width: 330px;
  border: 2px solid var(--main-green-color);
  padding: 10px 40px;
  outline: none;
  color: white;
  border-radius: 24px;
  transition: 0.25s;
  cursor: pointer;
}
.loginForm input[type="submit"]:hover {
  background: var(--fill-green-color);
}

.loginForm p,
#loginLink {
  color: #a9a9a9;
  color: var(--main-grey-color);
  font-size: small;
  text-decoration: none;
  /* display: inline-block;  makes transform work */
  transition: all 0.5s ease-in-out;
}

.loginForm p #loginLink {
  color: #fff;
  color: var(--main-white-color);
}

.loginForm p #loginLink:hover {
  /* transform: scale(1, 1.03); */
  font-size: medium;
  color: #14b4fe;
  color: var(--main-blue-color);
}

/* Logout */
.logoutBtn {
  width: 80px;
  /*height: 25px; */
  border: 2px solid #fff;
  border: 2px solid var(--main-white-color);
  margin-top: 6px;

  display: flex;
  justify-content: center;
  text-align: center;
  border-radius: 20px;
  border-radius: var(--card-br);
}
.logoutBtn:hover {
  background-color: #fff;
  background-color: var(--main-white-color);
  color: #111;
  border: 2px solid #a9a9a9;
  border: 2px solid var(--main-grey-color);
}
.logoutBtn > a {
  margin-bottom: 2px;
  font-family: Poppins, sans-serif;
  font-weight: bold;
  color: #a9a9a9;
  color: var(--main-grey-color);
  width: 100px;
  text-decoration: none;
}
.logoutBtn > a:hover {
  font-family: Poppins, sans-serif;
  font-weight: bold;
  color: #14b4fe;
  color: var(--main-blue-color);
}

